define('dummy/controllers/application', ['exports', 'ember', 'ember-validations'], function (exports, Ember, EmberValidations) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend(EmberValidations['default'], {

    username: '',
    age: '',
    email: 'hello world',

    validations: {
      username: {
        presence: true,
        length: { minimum: 5 }
      },
      age: {
        numericality: true
      },
      email: {
        format: { 'with': /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/, allowBlank: true, message: 'must be a valid email' }
      }
    }

  });

});